			getVerifyCode: function() {
				var myreg=/^[1][1,2,3,4,5,7,8,9][0-9]{9}$/;
				if (!myreg.test(this.mobile)){
					uni.showToast({ title: '请正确填写手机号码', icon : "none"});
				    return false;
				}else{
					var _this = this;
					uni.request({
						url: this.apiServer + '/user/verify',
						method: 'POST',
						header: {
							'content-type': 'application/x-www-form-urlencoded'
						},
						data: {
							mobile: _this.mobile
						},
						success: res => {
							if (res.data.code === 0) {
								uni.showToast({
									title: '验证码已发送'
								});
								_this.disabled = true;
								console.log(_this.disabled);
								const TIME_COUNT = 60;
								if (!this.timer) {
									this.count = TIME_COUNT;
									this.show = false;
									this.timer = setInterval(() => {
										if (this.count > 0 && this.count <= TIME_COUNT) {
											this.count--;
										} else {
											this.show = true;
											clearInterval(this.timer);
											this.timer = null;
										}
									}, 1000)
								};
							} else {
								uni.showModal({
									title: '提示',
									content: res.data.msg
								});
							}
						}
					});
				}
				
			},
			checkCode: function() {
				var _this = this;
				console.log(_this.verifyCode);
				uni.request({
					url: this.apiServer + '/user/check',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded'
					},
					data: {
						mobile: _this.mobile,
						verifyCode: _this.verifyCode
					},
					success: res => {
						console.log(res.data);
						if (res.data.code === 0) {
							uni.navigateTo({
								url: '../password/password?mobile=' + _this.mobile
							});
						} else {
							uni.showModal({
								title: '提示',
								content: res.data.msg
							});
						}
					}
				});
			}
		}
	}
